  g.note.playing {
    fill: crimson;
  }

  .row, #index {
    display: flex;
    flex-direction: row;
  }

  .cell, .cellDFT, .indexcell, .indexcellDFT {
    border: .5px dashed gray;
    width: 1em;
    height: 1em;
  }

  .cellDFT, .indexcellDFT {
    width: 2em !important;
  }

  .indexcell, .indexcellDFT {
    border: .5px solid transparent !important;
  }

  .indexcell > span, .indexcellDFT > span {
    font-style: italic;
    font-size: 0.5em;
    color: gray;
  }

  .cellDFT > span {
    font-size: x-small;
  }

  .cell.active, .cellDFT.active {
    background-color: crimson;
  }

  .continuation.active  {
    background-color: springgreen;
  }

  .EncodingViewer {
    display: block;
  }

  #encodingContent {
    display: flex;
    margin: 0 5%;
  }

  #pianorollEncoding, .Pianoroll, #dftEncoding, .DFT {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap-reverse;

    overflow-x: scroll;
    overflow-y: hidden;
  }